.nav-bar{
    background-color:#253957;
    position: relative;
    z-index: 1;
}

.fixed .nav-bar{
    background-color:transparent;
}

.nav-block{
    width: 100%;
}

.nav__profile-data{
    position: relative;
}

.nav__profile-data__avatar {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    object-fit: cover;
}

.nav__profile-data__main{
    cursor: pointer;
    align-items: center;
    display: flex;
    min-width: 200px;
}


.nav__profile-data__dropdown{
    padding: 10px;
    position: absolute;
    top: 75px;
    background-color: #253957;
    width: 100%;
    display: none;
}

.nav__profile-data__dropdown.active{
    display: block;
}

.nav__profile-data__options-btn{
    margin-left: 10px;
}

.nav{
    width: 100%;
    display: flex;
    justify-content: space-between;
    color: #fff;
    align-items: center;
    vertical-align: center;
    padding: 10px 0;
}

.nav .nav__right-side,
.nav .nav__left-side,
.nav__search{
    align-items: center;
    display: flex;
}


nav .nav-el:not(:first-child){
    margin-left: 25px;
}

.nav__search img{
    margin-left: 10px;
    width: 25px;
    transform: rotate(180deg);

}
.search-panel{
    -webkit-box-shadow: 0px -10px 30px 0px #323232bf;
    -moz-box-shadow:    0px -10px 30px 0px #323232bf;
    box-shadow:         0px -10px 30px 0px #323232bf;
}

.search-panel__wrapper{
    height: 100px;
    z-index: 0;
}


.search-panel{
    margin-top: -100px;
    transition: .5s;
    z-index: -10;
    opacity: 0;
}

.search-panel.active{
    background-color: #EFF5FF;
    display: block;
    margin-top: 0;
    z-index: -1;

    transition: .5s;
    opacity: 1;
}

.nav__search img{
    transition: .5s;
}

.nav__search{
    padding: 0 20px;
    height: 40px;
}

.nav__search.active img{
    transform: rotate(0);
    transition: .5s;
}

.nav__search img{
    transition: .5s;
}

.nav__search:hover img{
    margin-top: -5px;
    transition: .5s;
    animation-delay: .5s;
    animation: swing .8s infinite ease;
}

@keyframes swing{
    0% { 
        margin-top: -5px;
    }

    50% { 
        margin-top: 10px;
    }

    100% { 
        margin-top: -5px;
    }
    
}



